import React, { useEffect, useState, useCallback, useContext, useMemo, useReducer } from 'react';
import { StaticContext } from 'react-router';
import { Link, RouteComponentProps } from 'react-router-dom';
import { NotifContext } from './Contexts';

import * as regexes from './utils/regexes';
import styles from './css/home.module.css';
import { GET, socket } from './utils/requests';
import { Notif, TournamentStatus } from './interfaces';
import { FaAngleRight } from 'react-icons/fa';
import { GoPrimitiveDot } from 'react-icons/go'
import { MdAnnouncement } from 'react-icons/md';
import { getLogo } from './components/NotifBox';
import NotFound from './404';
import './css/reddit.css';

export default function Home(props: RouteComponentProps<any, StaticContext, any>) {

	const [failed, fail] = useReducer(() => true, false);
	const [connected, setConnected] = useState(0);
	const [tournaments, setTournaments] = useState(null as null | TournamentStatus[]);
	const [notifications, setNotifications] = useState([] as Notif[]);
	const select = useContext(NotifContext);

	const updateTournaments = useCallback(() => GET({ url: '/tournaments' })
		.then(setTournaments)
		.catch(() => fail()), [fail, setTournaments]);

	const updateNotifications = useCallback(() => GET({ url: '/notifications' })
			.then((n) => setNotifications(n.sort((a: Notif, b: Notif) => new Date(b.createdAt).valueOf() - new Date(a.createdAt).valueOf()))), [setNotifications]);

	useEffect(() => {
		updateTournaments();
		updateNotifications();
	}, [updateTournaments, updateNotifications]);

	useEffect(() => {
		socket.on('connected_count', (count: number) => setConnected(count));
	}, [setConnected]);

	useEffect(() => {
		if (connected) return;
		let x = setTimeout(() => socket.emit('request_online'), 1000);
		return () => clearTimeout(x);
	}, [connected]);

	function HomeNotifications(n: Notif, i: number) {
		
		let icon = <MdAnnouncement /> as JSX.Element | null;
		let domain = (n.link || '').match(regexes.link);
		if (domain) icon = getLogo(domain[1]);
		return (
			<tr className={styles.notificationRow} key={['notification', i].join('.')}>
				<td>
					<div onClick={() => select(n)}>												
						{n.link ?
							<a className={styles.iconLink} href={n.link} rel='noopener noreferrer' target='_blank'>
								{icon}
							</a> :
							<div>icon</div>
						}												
						<div>
							<span className='bold'>{n.title}</span>
						</div>
						<div className={styles.notifContent}>
							{n.content}
						</div>
						<div className={styles.expando}>
							<FaAngleRight />
						</div>
					</div>
				</td>
			</tr>
		);
	}

	const [upcoming, live, finished] = useMemo(() => {
		let [u, l, f] = [[], [], []] as TournamentStatus[][];
		for (let t of tournaments || []) {
			if (!t.round) u.push(t);
			else if (t.active) l.push(t);
			else f.push(t);
		}
		return [u, l, f];
	}, [tournaments]);

	useEffect(() => {
		document.querySelectorAll('.reddit-poll input').forEach(p => p.setAttribute('disabled', 'true'));
		let poll = document.querySelector('.reddit-poll');
		if (!poll) return;
		let prev = document.getElementById('reddit-card');
		if (!prev) return;
		let a = prev.querySelector('a');
		if (!a) return;
		let href = a.href;
		let x = () => window.open(href, '_blank');
		poll.addEventListener('click', x);
		return () => poll?.removeEventListener('click', x);
	})
	
	if (failed) return <NotFound />;

	return (<>
        <div id='main' className={'animated-' + props.location.state?.direction}>
			<div className={styles.container}>
				<div className={styles.title}>
					<h1><span className='bold'>Results</span> by ScorchChess</h1>
					<h3>Your universal platform for results and pairings. Attractive. Automated. Effortless.</h3>
				</div>
				<div className={styles.col1}>
					
					<div id='reddit-card'>
					<blockquote className="reddit-card"><a href="https://www.reddit.com/r/scorchchess/comments/hkjcc3/how_often_do_you_notice_a_bug_on_scorchchess_has/">How often do you notice a bug on ScorchChess? Has it affected your ability to use a feature? Leave any descriptions in the comments!</a> from <a href="http://www.reddit.com/r/scorchchess">r/scorchchess</a></blockquote>
						<div className='reddit-poll'>
							<div><label className="_3nN8pAR2tWvzxcstONuzMH">
								<input className="_1d_v-dIPu8uuwF0UTaQNmF" type="radio" name="proposal" value="0" />
								<div className="_3grr_S6IK0w2iyC7Ocv5u9">
									Frequently
								</div>
							</label>
							<label className="_3nN8pAR2tWvzxcstONuzMH">
								<input className="_1d_v-dIPu8uuwF0UTaQNmF" type="radio" name="proposal" value="1" />
								<div className="_3grr_S6IK0w2iyC7Ocv5u9">
									Somtimes
								</div>
							</label>
							<label className="_3nN8pAR2tWvzxcstONuzMH">
								<input className="_1d_v-dIPu8uuwF0UTaQNmF" type="radio" name="proposal" value="2" />
								<div className="_3grr_S6IK0w2iyC7Ocv5u9">
									Rarely
								</div>
							</label>
							<label className="_3nN8pAR2tWvzxcstONuzMH">
								<input className="_1d_v-dIPu8uuwF0UTaQNmF" type="radio" name="proposal" value="3" />
								<div className="_3grr_S6IK0w2iyC7Ocv5u9">
									Not at all
								</div>
							</label>
						</div>
						</div>
					</div>

					<div className={['container', styles.tournaments].join(' ')}>
						<table className={[styles.tableAnnounce].join(' ')}>
							<thead>
								<tr className={styles.sizer}><th /><th /><th /><th /></tr>
								<tr><th colSpan={4} className={styles.sectionHeader}>Upcoming Tournaments</th></tr>
								<tr className={styles.sectionHeader}><th>Name</th><th>Started</th><th>Round</th><th /></tr>
							</thead>
							<tbody>
								{upcoming
									.sort((a, b) => new Date(b.createdAt).valueOf() - new Date(a.createdAt).valueOf())
									.map(HomeTournament)
								}				
							</tbody>
						</table>
					</div>
					<div className={['container', styles.announcements].join(' ')}>
						<table className={[styles.tableAnnounce].join(' ')}>
							<thead>
								<tr>
									<th className={styles.sectionHeader}>
										Announcements
									</th>
								</tr>
							</thead>
							<tbody>
								{notifications.map(HomeNotifications)}
							</tbody>
						</table>
					</div>
				</div>
				<div className={styles.col2}>
					<div className={['container', styles.tournaments].join(' ')}>
						<table className={[styles.tableAnnounce].join(' ')}>
							<thead>
								<tr className={styles.sizer}><th /><th /><th /><th /></tr>
								<tr><th colSpan={4} className={[styles.sectionHeader, styles.live].join(' ')}>Live Tournaments</th></tr>
								<tr className={styles.sectionHeader}><th>Name</th><th>Started</th><th>Round</th><th /></tr>
							</thead>
							<tbody>
								{live
									.sort((a, b) => new Date(b.createdAt).valueOf() - new Date(a.createdAt).valueOf())
									.map(HomeTournament)
								}				
							</tbody>
						</table>
					</div>
					<div className={['container', styles.tournaments].join(' ')}>
						<table className={[styles.tableAnnounce].join(' ')}>
							<thead>
								<tr className={styles.sizer}><th /><th /><th /><th /></tr>
								<tr><th colSpan={4} className={styles.sectionHeader}>Finished Tournaments</th></tr>
								<tr className={styles.sectionHeader}><th>Name</th><th>Started</th><th>Round</th><th /></tr>
							</thead>
							<tbody>
								{finished
									.sort((a, b) => new Date(b.updatedAt).valueOf() - new Date(a.updatedAt).valueOf())
									.map(HomeTournament)
								}				
							</tbody>
						</table>
					</div>
				</div>
				<div className={socket.connected ? styles.connected : styles.disconnected}>
					<GoPrimitiveDot /> {connected || '-'} user{connected >= 2 ? 's' : ''} online
				</div>
				<a href='https://beta.scorchapp.co.uk' rel='noopener noreferrer' target='_blank' className={[styles.new].join(' ')}>
					Create New Tournament
					<FaAngleRight />
				</a>
			</div>				
		</div>
	</>)
}

function HomeTournament(tourn: TournamentStatus, i: number) {

	let dateStarted = new Date(tourn.createdAt).toString();

	return (
		<tr key={[tourn.id, i].join('.')} className={styles.rowLink}>
			<td><Link className={styles.tournamentLink} to={'/t/' + tourn.id}>
				{tourn.name || tourn.id}
			</Link></td>
			<td><Link className={styles.tournamentLink} to={'/t/' + tourn.id}>
				{dateStarted.slice(0, 11)}
			</Link></td>
			<td><Link className={styles.tournamentLink} to={'/t/' + tourn.id}>
				{tourn.round}
			</Link></td>
			<td><Link className={styles.tournamentLink} to={'/t/' + tourn.id}>
				<FaAngleRight />
			</Link></td>
		</tr>
	);
}