import React, { ReactElement, useCallback, useState, useReducer, useEffect, useMemo, useContext } from 'react';
import { Link } from 'react-router-dom';
import ChessCom, { Chess } from '../chess/ChessCom';
import * as icons from '../assets/icons/';
import * as regexes from '../utils/regexes';
import { FaLink, FaSearch, FaDownload, FaChessBoard, FaClock, FaFilter, FaAngleDown, FaEye } from 'react-icons/fa';
import { Result, Player, TournamentStatus, TournamentSettings, Team, ProfileData } from '../interfaces';
import { GET } from '../utils/requests';
import styles from '../css/pairings.module.css';
import AutoComplete from '../components/AutoComplete';
import PercentageCompleted from './PieChart';
import Match, { MatchConstructor } from '../models/Match';
import { shuffle, getFlag } from '../utils/prototype';
import { ChessTeam } from 'chess-web-api';
import Dropdown from '../components/Dropdown';
import { LoginContext } from '../App';
import { Game } from '../resources/result';
import { useStateWithLabel } from '../utils/hooks';

export interface PairingsProps {
	direction: string
	id: string
	status: TournamentStatus
	playerDict: Map<string, Player>
	settings: TournamentSettings
}

export default function Pairings(props: PairingsProps & {
	round: number
	profile: ProfileData
}): ReactElement | null {

    const [pairings, setPairings] = useState([] as Result[]);
	const [loaded, hasLoaded] = useState(false);
	const [fetched, hasFetched] = useReducer((state: string[], action: string) => {
		let s = state.slice(0);
		s.push(action);
		return s;
	}, []);
    const [matchDict, setMatch] = useReducer((state: {[key: string]: Match}, action: MatchConstructor | {id: string, scored: null}) => {
		let res = Object.assign({}, state);
		if (action.scored === null) {
			res[action.id] = null as any as Match;
			return res;
		}
		let match = action as MatchConstructor;
        res[action.id] = new Match(match, match.round);
        return res;
	}, {});
	const matches = useMemo(() => Object.values(matchDict), [matchDict]);

    const [expanded, setExpanded] = useReducer((state: string[], action: string[] | string) => {
		if (Array.isArray(action)) return action;
		let list = state.slice(0) || [];
		if (state.includes(action)) list.splice(list.indexOf(action), 1);
		else list.push(action);
		return list;
	}, [decodeURIComponent(window.location.hash.slice(1).split('.').slice(0, 2).join('.'))]);
	const [gameDict, setGameDict] = useState({} as {[key: string]: Game[]});
	const fetchGames = useCallback((ids?: string[]) => {
		return GET({
			url: ['', 'tournament', props.id, 'getPairings'].join('/'),
			params: {
				round: props.round,
				ids: ids?.join(','),
				games: true
			}
		}).then(arr => setGameDict(arr.reduce((acc: {[key: string]: Game[]}, curr: { id: string, games: Game[]}) => {
			acc[curr.id] = curr.games;
			return acc;
		}, {})));
	}, [props.id, props.round, setGameDict]);
	
	useEffect(() => {
		hasLoaded(false);
		GET({
			url: '/tournament/' + props.id + '/getPairings',
			params: { round: props.round }
		})
			.then((data) => {
				setPairings(data);
				hasLoaded(true);
				return data;
			})
			.then((data: Result[]) => {
				if (window.location.hash) return;
				if (props.round >= props.status.round) return;
				//fetchGames().then(() => setExpanded(data.filter(m => m).map(m => m.id)));
			})
			.catch(() => {})
	}, [setPairings, props.round, props.id, fetchGames, props.status.round]);
	
	const [choices, setChoices] = useState([] as Player[]);
	const headers = ['White', 'White score', 'Black', 'Black score', 'Game link', 'Time'] as string[];
	const [csv, setCSV] = useState([] as any[][]);

    const populatematchDict = useCallback(async (p: Result[], round: number) => {
		if (!p.length) return;
        for (let r of p) {
			let { id, idW, idB, link, linkSource, linkType, fetched, board} = r;
			let w = props.playerDict.get(idW.trim()) as Player;
			let b = props.playerDict.get(idB.trim()) as Player;
			if (!w && !b) {
				setMatch({id, scored: null});
				continue;
			}
			let parsedLink = '';
			let teamID = undefined;
			if (linkSource === 'chessCom' && linkType === 'team') {
				let matches = link?.match(regexes.chessCom) as RegExpMatchArray;
				if (matches) parsedLink = 'https://www.chess.com/club/matches/live/' + matches[1].trim();
				teamID = matches ? Number(matches[1]) : undefined;
			}
			let scored = w?.histories[round]?.match || b?.histories[round]?.match;
			let match = {
				tournamentId: props.status.id,
				id,
				teamID,
				link: parsedLink,
				w,
				b,
				board,
				round,
				scores: (w?.histories[round]?.game || b?.histories[round]?.game) ? [
					w?.histories ? w?.histories[round]?.game : null,
					b?.histories ? b?.histories[round]?.game : null,
				] : fetched?.scores || [null, null],
				scored: !!scored,
				linkSource,
				linkType,
				startTime: fetched?.startTime,
				endTime: fetched?.endTime,
				bodyStatus: fetched?.isFinished ? 'finished' : ''
			} as MatchConstructor;
			setMatch(match);				
        }
	}, [setMatch, props.playerDict, props.status.id]);

	useEffect(() => {
		Chess.clearQueue();
		setPairings([]);
	}, [setPairings, props.round]);
	
    useEffect(() => {
        populatematchDict(pairings, props.round);
		/* eslint-disable-next-line react-hooks/exhaustive-deps */
	}, [populatematchDict, pairings]);

	const getDisplayName = useCallback((opp: Player): string => {
        if (!opp || !opp.id) return '\u200b';
		if (opp.id === 'bye') return 'BYE';
		let displayName: string;
		if (props.settings.competitors === 'individual') displayName = [opp.firstName, opp.lastName].join(' ');
		else displayName = opp.firstName;
		if (props.settings.showRatings) displayName += ' (' + opp.rating + ')'
		return displayName;
	}, [props.settings.competitors, props.settings.showRatings]);

	const [fetchMatch, chooseMatch] = useState(null as null | Match);
	useEffect(() => {
		let length = Object.values(matchDict).length;
		if (!length) return;
		if (!pairings.length) return;
		if (length < pairings.length) return;
		if (fetchMatch && !fetched.includes(fetchMatch.id)) return chooseMatch(fetchMatch);
		let found = shuffle(Object.values(matchDict)).find((m: Match) => {
			if (!m) return false;
			if (!m.link) return false;
			if (fetched.includes(m.id)) return false;
			if (!m.startTime && !m.endTime) return true;
			if (m.games && !Array.isArray(m.games)) return true;
			if (m.endTime) {
				if (!m.scores || m.scores.every((s: number | undefined) => !s)) return true;
				if (Math.random() < 0.01) return true;
				return false;
			} else
			if (m.startTime) {
				if (Date.now() > m.startTime) {
					if (Math.random() < 0.25) return true;
					return false;
				}
				if (Math.random() < 0.01) return true;
				return false;
			} return true;
		});
		if (found) chooseMatch(found);
		else chooseMatch(null)
	}, [matchDict, pairings, chooseMatch, fetchMatch, fetched]);

	const [visible, setVisible] = useState('all');

	const isLoggedIn = useContext(LoginContext);
	const [profile, setProfile] = useState(undefined as Team & ProfileData | null | undefined);
	const updateProfile = useCallback(async () => {
		if (!regexes.username.test(props.profile.username)) return setProfile(null);
		GET({ url: '/user/' + props.profile.username })
			.then(setProfile);
	}, [setProfile, props.profile.username])
	useEffect(() => {
		if (!isLoggedIn) return;
		updateProfile();
	}, [updateProfile, isLoggedIn]);
		
	const rows = useMemo(() => {
		let _csv = [] as any[][];
		let r = Object.values(matchDict).sort((a, b) => {
			if (!a) return -1;
			if (!b) return 1;
			return a.board - b.board
		}).map((match, i) => {
			if (!match) return null;
			if (match.round !== props.round) return null;

			if (match.id in gameDict) match.games = gameDict[match.id];
			let displayTime = match.startTime || match.registrationTime;
			let date = new Date(displayTime || 0).toString();
			let data = [
				getDisplayName(match.w),
				match.scores[0] ?? ((match.isLive || match.isFinished) && match.teams && (match.w?.chessCom || match.b?.chessCom) ? match.teams[0].score : ''),
				getDisplayName(match.b as Player),
				match.scores[1] ?? ((match.isLive || match.isFinished) && match.teams && (match.w?.chessCom || match.b?.chessCom) ? match.teams[1].score : ''),
				displayTime ? date.slice(0, 10) + '\n' + date.slice(16, 21): '',
				match.link,
				match.w ? match.w.nationality : '',
				match.b ? match.b.nationality : ''
			] as string[];

			_csv[i + 1] = data;
			return <Row
				key={['row', i].join('.')}
				data={data}
				match={match}
				setMatch={setMatch}
				setFinished={hasFetched}
				round={props.round}
				fetch={fetchMatch ? fetchMatch.id === match.id : false}
				expanded={expanded.includes(match.id)}
				setExpanded={setExpanded}
				highlight={isLoggedIn && (match.w?.id === profile?.userId || match.b?.id === profile?.userId || profile?.teams?.some(t => t && (t.id === match.w.teamId || t.id === match.b.teamId)))}
				hidden={(visible !== 'all' && match.status !== visible) || (!!choices.length && !choices.some(c => c.id === match.w?.id || c.id === match.b?.id))}
				settings={props.settings}
				fetchGames={fetchGames}
			/>
		});
		setCSV(_csv);
		return r;
	}, [gameDict, getDisplayName, fetchMatch, setExpanded, setCSV, expanded, choices, matchDict, props.round, props.settings, hasFetched, visible, profile, isLoggedIn]);

	const [scrolled, setScrolled] = useStateWithLabel(false, 'scrolled');
	/* eslint-disable-next-line react-hooks/exhaustive-deps */
	useEffect(() => {
		if (scrolled) return;
		if (!window.location.hash) return;
		let id = window.location.hash.slice(1);
		let elem = document.getElementById(id);
		if (elem) {
			elem.scrollIntoView({
				behavior: 'auto'
			});
			setScrolled(true);
		}
	})

	const handleDownload = useCallback(() => {
		const element = document.createElement('a')
		const fileContent = headers.join(',') + '\n' + csv.map(line => line.join(',')).join('\n');
		const fileFormat = 'csv'
		const file = new Blob([fileContent], {type: 'text/csv'});
		const tourName = (props.status.name.match(regexes.lowerCase) || []).join('');
		const date = new Date(Date.now()).toString().slice(4, 15);
	
		element.href = URL.createObjectURL(file)
		element.download = `${tourName}_${date}_${[props.round]}.${fileFormat}`
		element.click()
	}, [csv, props.round, headers, props.status.name]);

	const CompletedChart = useMemo(() => {
		return <PercentageCompleted matches={matchDict} round={props.round} total={pairings.length}/>;
	}, [matchDict, props.round, pairings]);

	return <>
		<section className={pairings.length ? 'animated-' + props.direction : ''} style={{display: !pairings.length ? 'none' : undefined}}>
			<div className={styles.pieMatrix}>
				<div className={['button', styles.timezone].join(' ')}>
					<FaClock />{new Date(Date.now()).toString().slice(25)}				
				</div>
				<Dropdown
					current={visible}
					options={[
						'all',
						'aborted',
						'pending',
						'scheduled',
						'registered',
						'live',
						'finished',
						'confirmed'
					].filter(s => s === 'all' || matches.some(m => m && m.round === props.round && m.status === s))}
					resolve={setVisible}
					className={styles.statusPicker}
					headerIcon={<FaFilter />}
				/>
				<div className={styles.pie}>
					{CompletedChart}
				</div>
				<div className={styles.searchBar}>
					<FaSearch />
					<AutoComplete
						onChange={(options: Player[]) => setChoices(options)}
						onSubmit={() => Promise.resolve()}
						action=''
						options={Array.from(props.playerDict.values())}
						convert={(p: Player) => getDisplayName(p)}
						styles={styles}
						placeholder={'Search'}
					/>
				</div>
				<div className={['button', styles.download].join(' ')} onClick={handleDownload}>
					<FaDownload />Download
				</div>
				<div className={['button', styles.expandAll].join(' ')} onClick={() => {
					if (matches.length === expanded.length) setExpanded([]);
					fetchGames().then(() => setExpanded(matches.map(m => m.id)))
				}}>
					<FaEye />{matches.length === expanded.length ? 'Hide' : 'View'} all scores
				</div>
				{!isLoggedIn ? null : <div className={['button', styles.jumpTo].join(' ')} onClick={() => document.getElementsByClassName('highlighted')[0].scrollIntoView()}>
					<FaAngleDown />My pairing
				</div>}
			</div>
		</section>
		<section className={[pairings.length ? 'animated-' + props.direction : '', styles.table].join(' ')}>			
			<table className='download-table full-width pairings'>
				<thead>
					<tr>
						<td style={{width: '5%'}}>Fed</td>
						<td style={{width: '22.5%'}}>White</td>
						<td style={{width: '12.5%'}}>Link</td>
						<td style={{width: '5%'}}></td>
						<td style={{width: '10%'}}>Start Time</td>
						<td style={{width: '5%'}}></td>
						<td style={{width: '12.5%'}}>Watch Live</td>
						<td style={{width: '22.5%'}}>Black</td>
						<td style={{width: '5%'}}>Fed</td>
					</tr>
				</thead>
				<tbody>
					{loaded && !pairings.length ? <tr><td colSpan={100}>Draw has not yet taken place!</td></tr> : null}                       
					{rows}
				</tbody>
			</table>
		</section>
	</>;

}

interface RowProps {
	data: any[]
	match: Match
	setMatch: (match: Match) => void
	setFinished: (id: string) => void
	hidden: boolean
	fetch: boolean
	round: number
	expanded: boolean
	setExpanded: (id: string) => void
	settings: TournamentSettings
	highlight?: boolean
	fetchGames: (ids?: string[] | undefined) => Promise<void>
}

const def = [] as ChessTeam[];
function Row(props: RowProps) {

	const match = props.match;
	const data = props.data;
	const [hasExpanded, cache] = useState(match.isLive);
	useEffect(() => {
		if (props.expanded && !hasExpanded) cache(true);
	}, [props.expanded, hasExpanded, cache]);

	const [shouldSync, sync] = useReducer((state: boolean, action: boolean) => typeof action !== 'undefined' ? action : true, false);
	
	return (
		<>
			<tr id={match.id} className={[styles.row, props.hidden ? 'collapsed' : '', props.highlight ? 'highlighted' : ''].join(' ')} style={{
				visibility: props.hidden ? 'collapse' : undefined
			}}>
				<td>
					<div className={styles.flag}>{getFlag(data[6])}</div>
				</td>
				<td id={match.w?.id}>
					<Link to={['', props.settings.competitors === 'individual' ? 'player' : 'team', match.w?.id].join('/')} className='playerName'>{data[0]}</Link>
				</td>
				<td className={styles.inner} id={match.link?.split('/').slice(-1).join('/')}>
					{match.link ? <a className='button' href={match.link} target='_blank' rel='noopener noreferrer'>
						<FaLink />
						<div>chess.com</div>
					</a> : null}
				</td>     
				<td className={styles.inner}>
					<div className={[styles.score, styles[match.status], match.status].join(' ')}>
						{match.isFinished || match.isLive ? data[1] : null}
					</div>
				</td>
				<td style={{whiteSpace: 'pre-line'}}>{data[4] || '-'}</td>
				<td className={styles.inner}>
					<div className={[styles.score, styles[match.status], match.status].join(' ')}>
						{match.isFinished || match.isLive ? data[3] : null}
					</div>
				</td>
				<td className={styles.inner}>
					{match.link && match.isScheduled ?
						<div className={['button', match.isLive ? styles.watchLive : ''].join(' ')} onClick={() => {
							if (!match.isFinished) return;
							if (!match.games?.length) props.fetchGames([match.id]).then(() => props.setExpanded(match.id));
							else props.setExpanded(match.id);
							if (window.location.hash && decodeURIComponent(window.location.hash).includes(match.id)) {
								window.history.pushState('', document.title, window.location.pathname + window.location.search);
							}
							else window.location.hash = '#' + match.id;
						}}>
							{match.isFinished ?
								<div>Scores</div> :
								<>
									{match.isLive ?
										<img className='icon' src={icons.watch} alt={'Watch games'}/> :
										<FaChessBoard />
									}
									<a href={'https://www.chess.com/live#tm=' + match.teamID} target='_blank' rel='noopener noreferrer'>
										{match.isLive ?
											'Watch live' :
											'Join Game'
										}
									</a>
								</>
							}
						</div> :
						null
					}
				</td>
				<td id={match.b?.id}>
					<Link to={['', props.settings.competitors === 'individual' ? 'player' : 'team', match.b?.id].join('/')} className='playerName'>{data[2]}</Link>
				</td>
				<td>
					<div className={styles.flag}>{getFlag(data[7])}</div>
				</td>
			</tr>
			{match.link ? <tr key={['row', 'dropdown'].join('.')} className={[props.hidden || !props.expanded  ? 'collapsed' : ''].join(' ')} style={props.hidden || !props.expanded ? {visibility: 'collapse'} : {}}>
				<td colSpan={100}>
					{match.linkSource === 'chessCom' ?
						match.linkType === 'team' ?
							<ChessCom
								id={match.id}
								match={match}
								setMatch={props.setMatch}
								setFinished={props.setFinished}
								sync={sync as (set?: boolean) => void}
								fetch={props.fetch || shouldSync}
								expanded={props.expanded}
								enforceBoards={match.players ? parseInt(match.players as any as string) : 0}
								round={props.round}
								teams={match.teams || def}
							/> :
							null :
						null
					}
				</td>
			</tr> : null}
		</>
	)
}