import React, { useEffect, useState } from 'react';
import Markdown from 'react-markdown';
import { GET } from '../utils/requests';
import { parseHtml, RouterLink } from '../utils/plugins';
import styles from './css/meta.module.css';

export default function Announcements(props: {
	id: string
}) {

	const [announcements, setAnnouncements] = useState([] as {
		announcement: string
		createdAt: string
		updatedAt: string
	}[]);

	useEffect(() => {
		if (!announcements.length) return;
		if (!window.location.hash) return;
		if (window.location.hash !== '#latest') return;
		window.location.hash = '#' + announcements[announcements.length - 1].createdAt
	}, [announcements]);

	useEffect(() => {
		GET({ url: ['', 'tournament', props.id, 'getAnnouncements'].join('/') }).then(setAnnouncements);
	}, [props.id, setAnnouncements]);

	return (<div className={styles.container}>
		{!announcements.length ? null : <div className={[styles.nav].join(' ')}>
			<div className={[].join(' ')}>
				{announcements.sort((a, b) => new Date(b.createdAt).valueOf() - new Date(a.createdAt).valueOf()).map((a) => {
					return <a href={'#' + a.createdAt} className={styles.date}>
						{new Date(a.createdAt).toString().slice(0, 21)}
					</a>
				})}
			</div>
		</div>}
		{announcements.length ?
			announcements.sort((a, b) => new Date(b.createdAt).valueOf() - new Date(a.createdAt).valueOf()).map((a) => {
				return <>
					<section className={styles.announcement} id={a.createdAt}>
						<Markdown
							source={a.announcement.trim()}
							escapeHtml={false}
							astPlugins={[parseHtml]}
							renderers={{
								link: RouterLink
							}}
						/>
						<div className={styles.dates}>
							<div>{new Date(a.createdAt).toString().slice(0, 21)}</div>
							<div>Last update: {new Date(a.updatedAt).toString().slice(0, 21)}</div>
						</div>
					</section>
					<hr className={styles.divider} />
				</>
			}) :
			<section className={styles.announcement}>
				This tournament has no announcements at this time.
			</section>
		}
	</div>)
}